import React from 'react'
import styled from 'styled-components'


const StyledButton = styled.a`
.btn-three {
    color: #FFF;
    transition: all 0.5s;
    position: relative;
    text-align: center;
    
}
.btn-three::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background-color: rgba(255,255,255,0.1);
    transition: all 0.3s;
}
.btn-three:hover::before {
    opacity: 0 ;
    transform: scale(0.5,0.5);
}
.btn-three::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    opacity: 0;
    transition: all 0.3s;
    border: 1px solid rgba(255,255,255,0.5);
    transform: scale(1.2,1.2);
}
.btn-three:hover::after {
    opacity: 1;
    transform: scale(1,1);
}

@media screen and (max-width: 640px) {
 
    .btn-three {
    color: #FFF;
    transition: all 0.5s;
    position: relative;
    text-align: center;
    width: 25%;
    height: 2.5rem;
    
}

}



`


const Buttons = ({children, link}) => {
    return (
        <StyledButton href={link}>
            <div className='btn-three'>
            {children}
            </div>
        </StyledButton>
    )
}

export default Buttons
